<template>
    <div class='container'>
        <div>
            <h1>Список задач</h1>
            <h6 :data='todo_total'
                class='text-left'>Всего задач: {{ todo_total }}</h6>
            <h6 :data='todo_not_completed'
                class='text-left'>Не выполнено: {{ todo_not_completed }}</h6>
            <div id='information-div'>
                <div class='confirm'
                     v-bind:class="{ 'confirm-hide' : message != '', 'hide' : message != '' }">
                    <p :message='message'>{{ message }}</p>
                </div>
                <div class='text-right add-button'>
                <b-button type="button"
                        id='add_task'
                        class='btn btn-success btn-sm'
                        @click="$bvModal.show('add-task-form')">
                    Добавить задачу
                </b-button>
                </div>
            </div>
            <table class='table table-dark table-striped table-hover'>
                <thead class="thead-light">
                    <tr class='table-bordered'>
                        <th>№</th>
                        <th class="text-left">Описание</th>
                        <th>Выполнена?</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr scope="row" v-for='(todo, index) in todo_list'
                        :key='index'
                        v-bind:class="{ 'bg-success': todo.is_completed }">
                        <td>{{ index + 1 }}</td>
                        <td class="text-left">{{ todo.description }}</td>
                        <td>
                            <span v-if='todo.is_completed'>Выполнено</span>
                            <span v-else>Не выполнено</span>
                        </td>
                        <td>
                            <div class='btn-group'
                                 role='group'>
                                <button type='button'
                                        class="btn btn-success btn-sm"
                                        @click='finishTask(todo)'
                                        v-if='!todo.is_completed'>
                                    Завершить
                                </button>
                                &nbsp;
                                <button type='button'
                                        class="btn btn-danger btn-sm"
                                        @click='deleteTask(todo)'
                                        >
                                    Удалить
                                </button>
                            </div>
                        </td>
                    </tr>
                </tbody>
            </table>

            <b-modal id='add-task-form'
                     title='Добавить задачу'
                     hide-footer
                     ref='formModal'>
                <b-form @submit='sendAddForm'>
                    <b-form-input id='description_field'
                                  type='text'
                                  required
                                  placeholder='Описание'
                                  value=''
                                  v-model='addTaskForm.description'>
                    </b-form-input>
                    <div class="text-center">
                        <b-button type='submit'
                                  variant='outline-success'>
                            Добавить
                        </b-button>
                        <b-button type='reset'
                                  variant='outline-warning'
                                  id='reset-btn'>
                            Очистить поле
                        </b-button>
                    </div>
                </b-form>
            </b-modal>

        </div>
    </div>
</template>

<script>
import axios from 'axios';

// const dataURL = 'http://localhost:5000/api/tasks';
// const addTaskURL = 'http://localhost:5000/api/add-task';

const dataURL = 'https://fast-mountain-85669.herokuapp.com/api/tasks';
const addTaskURL = 'https://fast-mountain-85669.herokuapp.com/api/add-task';

export default {
  name: 'Todo',
  data() {
    return {
      todo_list: [],
      todo_total: 0,
      todo_not_completed: 0,
      addTaskForm: {
        description: '',
      },
      message: '',
    };
  },
  methods: {
    getTodoList() {
      axios.get(dataURL)
        .then((response) => {
          this.todo_list = response.data.tasks;
          this.todo_total = response.data.total;
          this.todo_not_completed = response.data.not_completed;
        })
        .catch((error) => {
          console.log('>>--- GetTaskError ---<<\n', error);
          alert('Во время загрузки произошла ошибка.\nПодробности в консоли.');
        });
    },
    sendAddForm(event) {
      event.preventDefault();
      this.$refs.formModal.hide();
      const newTask = {
        description: this.addTaskForm.description,
      };
      axios.post(addTaskURL, newTask)
        .then(() => {
          this.getTodoList();
          this.showAlert('Задача добавлена успешно');
        })
        .catch((error) => {
          console.log('>>--- AddTaskError ---<<\n', error);
          alert('Во время добавления задачи произошла ошибка.\nПодробности в консоли.');
        });
    },
    deleteTask(todo) {
      axios.delete(`${dataURL}/${todo.uid}`)
        .then(() => {
          this.getTodoList();
          this.showAlert('Задача удалена успешно');
        })
        .catch((error) => {
          console.log('>>--- DeleteTaskError ---<<\n', error);
          alert('Во время удаления задачи произошла ошибка.\nПодробности в консоли.');
        });
    },
    finishTask(task) {
      axios.put(`${dataURL}/${task.uid}`)
        .then(() => {
          this.getTodoList();
          this.showAlert('Задача изменена успешно');
        })
        .catch((error) => {
          console.log('>>--- CompleteTaskError ---<<\n', error);
          alert('Во время изменения задачи произошла ошибка.\nПодробности в консоли.');
        });
    },
    showAlert(message) {
      this.message = message;
      setTimeout(
        () => this.message = '',
        4000,
      );
    },
  },
  created() {
    this.getTodoList();
  },
};
</script>

<style>
    #add_task {
        margin-bottom: 10px;
        white-space: nowrap;
    }

    #description_field {
        margin-bottom: 10px;
    }

    #reset-btn {
        margin-left: 5px;
    }
    #information-div {
        display: flex;
        justify-content: space-between;
    }
    .confirm {
        width: 70%;
        padding-top: 4px;
    }
    .confirm-hide {
        background-color: lightgreen;
        border-radius: 5px;
        opacity: .7;
        height: 32px;
    }
    .add-button {
        width: 30%;
    }
</style>
